.select2-container--classic[dir="rtl"] {
	.select2-selection--single {
		.select2-selection__clear {
			float: left;
		}
		.select2-selection__arrow {
			border: none;
			border-right: 1px solid #089e60;
			border-radius: 0;
			border-top-left-radius: 4px;
			border-bottom-left-radius: 4px;
			left: 1px;
			right: auto;
		}
	}
	.select2-selection--multiple {
		.select2-selection__choice {
			float: right;
			margin-left: 5px;
			margin-right: auto;
		}
		.select2-selection__choice__remove {
			margin-left: 2px;
			margin-right: auto;
		}
	}
}
.select2-container--classic.select2-container--open {
	.select2-selection--single {
		border: 1px solid #089e60;
		.select2-selection__arrow {
			background: transparent;
			border: none;
			b {
				border-color: transparent transparent #f6edfb transparent;
				border-width: 0 4px 5px 4px;
			}
		}
	}
	.select2-selection--multiple {
		border: 1px solid #089e60;
	}
	.select2-dropdown {
		border-color: #089e60;
	}
}
.select2-container--classic.select2-container--open.select2-container--above {
	.select2-selection--single {
		border-top: none;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		background-image: -webkit-linear-gradient(top, #fff 0%, #eaedff 50%);
		background-image: -o-linear-gradient(top, #fff 0%, #eaedff 50%);
		background-image: linear-gradient(to bottom, #fff 0%, #eaedff 50%);
