	.select2-selection--single {
		border-bottom: none;
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		background-image: -webkit-linear-gradient(top, #eaedff 50%, #fff 100%);
		background-image: -o-linear-gradient(top, #eaedff 50%, #fff 100%);
